
import { SvgFire } from "../../../blocks/SvgIcon/SvgIcon";
import Contents from "../Contents";


export default function Favorite() {
    return (
        <Contents
            coverIcon={<SvgFire />}
            coverNotes={
                <p>
                    
                    <br></br>
                    
                    <br></br><br></br>
                    May the fires of hope always guide us
                    <br></br>
                    Fight for all that is beautiful in the world
                </p>
            }
            title={'favor'}
            menu
        />
    );
}